.modal{
    width: 882px;
    height: 568px;background-color: #ffffff;
    position: relative;
    display: flex;
    justify-content:space-between;
}

.modal__poster{
    position: fixed;
    top: 50px;
    left:30px;
    // justify-content: start;
}
.modal__parameters{
    display: block;
    // position: absolute;
    height: 100%;
    width: 396px;
    padding-top: 50px;
    margin-right: 30px;
    &--data{
        display: flex;
    }
    &--list{
        color:#8C8C8C;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.33;
        margin-top: 20px;
    }
    &--list-name:not(:last-child){
        margin-bottom: 10px;
    }
    &--list-status{
        position: relative;
        top: 21px;
        left: 83px;
    }
    &--value:not(:last-child){
        margin-bottom: 10px;
        color: #000000;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.33;
    }
    &--title{
    top: 50px;
    font-size: 30px;
    font-weight: 500;
    text-transform: uppercase;
    }
}
.modal__description{
    position: relative;
    display: block;
    &--title{
        margin-top: 20px;
    text-transform: uppercase;
    color:#000000;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.33;
    }
    &--about{
        height: 160px;
        margin-top: 10px;
        font-size: 12px;
        font-weight: 500;
        color: #000000;
        line-height: 1.66;
        word-wrap: break-word;
        overflow-y: auto;
    }
    &--buttons{
        display: flex;
        margin-top: 21px;
    }
    &--button:not(:last-child){
        margin-right: 15px;
    }
    &--button-action{
        border: 1px solid;
        border-color: #000000;
        background-color: transparent;
        border-radius: 5px;
        height: 44px;
        width: 136px;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 500;
        text-align: center;
        
        &:hover,
        &:focus{
            color: #ffffff;
            background-color: #FF6B01;
            border-color: #FF6B01;
            transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
            box-shadow: 0px 6px 12px rgba(255, 107, 1, 0.6);
        }
    }
}


